body {
  margin: 0;
  padding: 0;
}

.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  max-width: 500px;
}
.app__footer {
  flex-grow: 0;
  flex-shrink: 0;
}
.app__body {
  flex-grow: 1;
  position: relative;
}
.app__content {
  height: 100%;
  left: 0;
  overflow-y: auto;
  position: absolute;
  top: 0;
  width: 100%;
}

.navbar {
  background: #f0f0f0;
}
.navbar__list {
  display: flex;
  padding-left: 10px;
  padding-right: 10px;
}
.navbar__item {
  background: #dedede;
  border-left: 1px solid #f0f0f0;
  border-right: 1px solid #f0f0f0;
  color: #333;
  font-size: 24px;
  line-height: 50px;
  text-align: center;
  width: 80px;
}
.navbar__item--active {
  background: #333;
  color: #dedede;
}

.page {
  height: 100%;
  position: absolute;
  width: 100%;
}
.page__wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.page__title {
  background: #333;
  color: #dedede;
  font-size: 32px;
  font-weight: 600;
  padding-bottom: 20px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 20px;
}
.page__section {
  padding: 10px;
}
.page__section--nopadding {
  padding: 0;
}
.page__section--grower {
  flex-grow: 1;
  overflow-y: auto;
}

.charedit {

}
.charedit__title {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 10px;
}
.charedit__stats {
  max-width: 400px;
  width: 100%;
}
.charedit__stats-item {
  border: 1px solid #dedede;
  display: flex;
  line-height: 26px;
}
.charedit__stats-label {
  background: #f0f0f0;
  flex-basis: 60px;
  flex-grow: 0;
  text-align: center;
}
.charedit__stats-content {
  display: flex;
  flex-grow: 1;
}
.charedit__stats-rawval {
  position: relative;
}
.charedit__stats-rawval input {
  border: 0;
  border-right: 1px solid #dedede;
  font-size: 16px;
  height: 100%;
  left: 0;
  padding: 0;
  position: absolute;
  text-align: center;
  top: 0;
  width: 100%;
}
.charedit__stats-rawval,
.charedit__stats-bonus,
.charedit__stats-result {
  flex-grow: 1;
  flex-basis: 33.333%;
  text-align: center;
}

.charedit__stats-bonus--positive {
  color: #16a086;
}
.charedit__stats-bonus--negative {
  color: #c0392b;
}

.multiselect {
}
.multiselect__wrapper {
  border: 1px solid #f0f0f0;
}
.multiselect__item {
  align-items: center;
  border: 1px solid #f0f0f0;
  display: flex;
}
.multiselect__item-icon {
  flex-basis: 20px;
}
.multiselect__item-name {
  font-size: 18px;
  font-weight: 600;
}
.multiselect__item-description {
  flex-grow: 1;
  text-align: right;
}